// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import Router from "vue-router";
import VueI18n from "vue-i18n";

import VueResource from "vue-resource";
import VueHtml5Editor from "vue-html5-editor";

import iView from "iView";
import zhLocale from "iView/src/locale/lang/zh-CN";
import enLocale from "iView/src/locale/lang/en-US";

import App from "./App";
import router from "./router";

import "iView/dist/styles/iview.css";

Vue.use( VueI18n );
Vue.use( iView );
Vue.use( VueResource );

Vue.use( Router );

Vue.locale( "zh-CN", zhLocale );
Vue.locale( "en-US", enLocale );

Vue.use( VueHtml5Editor, {
	//	new VueHtml5Editor(options)
	//	global component name
	name			:	"vue-html5-editor",
	//	
	//	if set true,will append module name to toolbar after icon
	showModuleName	:	false,
	//	classfont-awesome
	//	custom icon class of built-in modules,default using font-awesome
	icons			:	{
		text			:	"fa fa-pencil",
		color			:	"fa fa-paint-brush",
		font			:	"fa fa-font",
		align			:	"fa fa-align-justify",
		list			:	"fa fa-list",
		link			:	"fa fa-chain",
		unlink			:	"fa fa-chain-broken",
		tabulation		:	"fa fa-table",
		image			:	"fa fa-file-image-o",
		hr				:	"fa fa-minus",
		eraser			:	"fa fa-eraser",
		undo			:	"fa-undo fa",
		"full-screen"	:	"fa fa-arrows-alt",
		info			:	"fa fa-info"
	},
	//	
	//	config image module
	image	:	{
		//	base64
		//	Url of the server-side,default null and convert image to base64
		server		:	null,
		//	
		//	the name for file field in multipart request
		fieldName	:	"image",
		//	  max file size
		sizeLimit	:	5 * 1024 * 1024,
		//	truetruelocalResizeIMG
		//	default true,if set to true,the image will resize by localResizeIMG (https	:/	/github.com/think2011/localResizeIMG)
		compress	:	true,
		//	
		//	follows are options of localResizeIMG
		width		:	1600,
		height		:	1600,
		quality		:	80,
		//	
		//	handle response datareturn image url
		uploadHandler( responseText ){
			//	default accept json data like  {ok:false,msg:"unexpected"} or {ok:true,data:"image url"}
			let json = JSON.parse( responseText );
			if( !json.ok ){
				alert( json.msg );
			} else {
				return json.data;
			}
		}
	},
	//	en-uszh-cn
	//	default en-us, en-us and zh-cn are built-in
	language 	: 	"zh-cn",
	//	
	i18n 		: 	{
		//	specify your language here
		"zh-cn"		: 	{
			"align"					: 	"",
			"image"					: 	"",
			"list"					: 	"",
			"link"					: 	"",
			"unlink"				: 	"",
			"table"					: 	"",
			"font"					: 	"",
			"full screen"			: 	"",
			"text"					: 	"",
			"eraser"				: 	"",
			"info"					: 	"",
			"color"					: 	"",
			"please enter a url"	: 	"",
			"create link"			: 	"",
			"bold"					: 	"",
			"italic"				: 	"",
			"underline"				: 	"",
			"strike through"		: 	"",
			"subscript"				: 	"",
			"superscript"			: 	"",
			"heading"				: 	"",
			"font name"				: 	"",
			"font size"				: 	"",
			"left justify"			: 	"",
			"center justify"		: 	"",
			"right justify"			: 	"",
			"ordered list"			: 	"",
			"unordered list"		: 	"",
			"fore color"			: 	"",
			"background color"		: 	"",
			"row count"				: 	"",
			"column count"			: 	"",
			"save"					: 	"",
			"upload"				: 	"",
			"progress"				: 	"",
			"unknown"				: 	"",
			"please wait"			: 	"",
			"error"					: 	"",
			"abort"					: 	"",
			"reset"					: 	""
		}
	},
	//	
	//	the modules you don"t want
	hiddenModules 	: 	[],
	//	
	//	keep only the modules you want and customize the order.
	//	can be used with hiddenModules together
	visibleModules 	: 	[
		"text",
		"color",
		"font",
		"align",
		"list",
		"link",
		"unlink",
		"tabulation",
		"image",
		"hr",
		"eraser",
		"undo",
		"full-screen"
	],
	//	examples
	//	extended modules
	modules 		: 	{
		//	omit,reference to source code of build-in modules
	}
});

/* eslint-disable no-new */
new Vue({
	el			:	"#app",
	router,
	template	:	"<App/>",
	components	:	{
		App
	}
});



