<style>
.productList .productImg {
	vertical-align: top;
	margin: 10px 0;
}
</style>

<template>
<div class="productList">
	<div class="ivu-article">
		<blockquote>
			<p class="clrFix">
				<Button type="ghost" icon="plus-circled" @click="toAddProduct"></Button>
			</p>
		</blockquote>
	</div>
	<Table :context="self" :columns="productColumn" :data="productData" stripe>
	</Table>
	<Modal
		v-model="modal"
		title="">
		<p></p>
		<p></p>
		<p></p>
	</Modal>
</div>
</template>

<script>
import router from "../router";
export default {
	data(){
		var width = window.innerWidth * 0.83 - 210;
		return {
			self				:	this,
			modal				:	false,
			searchType			:	"no",
			searchVal			:	"",
			productColumn		:	[
				{
					title			:	"",
					key				:	"logo",
					width			:	160,
					render( row ){
						const imgsrc = row.logo;
						return `<img class="productImg" src=${imgsrc} width="120px" height="88px"></img>`;
					}
				},
				{
					title			:	"",
					key				:	"name",
					width			:	width / 6,
					sortable		:	true
				},
				{
					title			:	"",
					key				:	"delivery",
					width			:	width / 6,
					sortable		:	true
				},
				{
					title			:	"",
					key				:	"firstName",
					width			:	width / 6,
					sortable		:	true
				},
				{
					title			:	"",
					key				:	"secName",
					width			:	width / 6,
					sortable		:	true
				},
				{
					title			:	"",
					key				:	"thirdName",
					width			:	width / 6,
					sortable		:	true
				},
				{
					title			:	"",
					key				:	"status",
					fixed			:	"right",
					width			:	130,
					sortable		:	true,
					render( row ){
						return row.status === 0 ? `<Tag type="dot"></Tag>` : `<Tag type="dot" color="green"></Tag>`;
					}
				},
				{
					title: "",
					key: "action",
					fixed: "right",
					width: 170,
					render( row, columns, index ){
						let ctrl;
						switch( row.status ){
							case 0:
								ctrl = ` <i-button type="primary" @click="onShelves( ${ index } )" size="small"></i-button> <i-button @click="delProduct( ${ index } )" type="error" size="small"></i-button>`;
								break;
							case 1:
								ctrl = ` <i-button type="warning" @click="offShelves( ${ index } )" size="small"></i-button> <Tooltip placement="top"><i-button type="error" size="small" disabled></i-button><div slot="content"></div></Tooltip>`;
								break;
						}
						return `<i-button type="ghost" size="small" @click="editProduct( row )"></i-button>` + ctrl;
					}
				}
			],
			productData		:	[]
		};
	},
	mounted : function(){
		let _this = this;
		_this.$http.post("/Interface/queryProductInfo", {
		}).then( function( res ){
			let data = res.data;
			switch ( data.retcode ){
				case 0:
					_this.productData = data.retdata;
					break;
				default:
					_this.$Message.error(data.retmsg);
					break;
			}
		});
	},
	methods: {
		toAddProduct(){
			router.push({
				name	:	"productEdit",
				params	:	{
					productId	:	"add"
				}
			});
		},
		editProduct( obj ){
			router.push({
				name	:	"productEdit",
				params	:	{
					productId	:	obj.id
				}
			});
		},
		onShelves( index ){
			let obj		=	this.productData[ index ],
				_this	=	this;
			_this.$Modal.confirm({
				title		:	"",
				content		:	"" + obj.name + "",
				onOk		:	() => {
					_this.$http.post("/Interface/onShelvesProduct", { productId : obj.id } ).then( function( res ){
						let data = res.data;
						switch ( data.retcode ){
							case 0:
								obj.status = 1;
								_this.$Message.info(data.retmsg);
							break;

							default:
								_this.$Message.error(data.retmsg);
							break;
						}
					});
				}
			});
		},
		offShelves( index ){
			let obj		=	this.productData[ index ],
				_this	=	this;
			_this.$Modal.confirm({
				title		:	"",
				content		:	"" + obj.name + "",
				onOk		:	() => {
					_this.$http.post("/Interface/offShelvesProduct", { productId : obj.id } ).then( function( res ){
						let data = res.data;
						switch ( data.retcode ){
							case 0:
								obj.status = 0;
								_this.$Message.info(data.retmsg);
							break;

							default:
								_this.$Message.error(data.retmsg);
							break;
						}
					});
				}
			});
		},
		delProduct( index ){
			let obj		=	this.productData[ index ],
				_this	=	this;
			_this.$Modal.confirm({
				title		:	"",
				content		:	"" + obj.name + "",
				onOk		:	() => {
					_this.$http.post("/Interface/delProduct", { productId : obj.id } ).then( function( res ){
						let data = res.data;
						switch ( data.retcode ){
							case 0:
								_this.productData.splice( index, 1 );
								_this.$Message.info(data.retmsg);
							break;

							default:
								_this.$Message.error(data.retmsg);
							break;
						}
					});
				}
			});
		}
	}
};
</script>



